import React, { Fragment } from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { navigateTo } from 'gatsby-link';
import withRoot from '../../withRoot';
import FacebookIcon from '../../../static/svgcomp/Facebook';
import GithubIcon from '../../../static/svgcomp/Github';
import InstagramIcon from '../../../static/svgcomp/Instagram';

import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';

const styles = theme => ({
  root: {
    backgroundImage: 'linear-gradient(to top, #48c6ef 0%, #6f86d6 100%)',
    height: '100%',
    minHeight: '100vh',
    boxSizing: 'unset',
  },
  appBar: {
    margin: '0 auto',
    paddingTop: 25,
    display: 'flex',
    maxWidth: 1000,
  },
  body: {
    margin: '0 auto',
    paddingTop: 25,
    maxWidth: 1000,
    display: 'block',
  },
  white: {
    color: '#FFF',
  },
  divRight: {
    marginLeft: 'auto',
    display: 'flex',
    alignItems: 'center',
  },
  title: {
    color: '#FFF',
    fontWeight: 400,
    textTransform: 'unset',
    borderRadius: 3,
  },
  iconHover: {
    color: '#FFF',
    width: 25,
    height: 25,
    cursor: 'pointer',
    '&:hover': {
      fill: 'rgba(255, 255, 255, 0.5)',
    },
  },
  [theme.breakpoints.down('md')]: {
    appBar: {
      maxWidth: 800,
    },
    body: {
      maxWidth: 800,
    },
  },
  [theme.breakpoints.down('sm')]: {
    appBar: {
      padding: '20px 20px 0px',
    },
    body: {
      padding: '20px 20px 0px',
    },
  },
  '@media screen and (max-width: 400px)': {
    divRight: {
      flexDirection: 'column-reverse',
    }
  }
});

class Frame extends React.Component {

  render() {
    const { classes } = this.props;
    return (
      <Fragment>
        <Helmet defaultTitle="adnanfajlur">
          <html lang="en" />
        </Helmet>
        <div className={classes.root}>
          <div className={classes.appBar}>
            <Typography className={classes.title} onClick={() => navigateTo('/')} style={{ display: 'flex', alignItems: 'center', cursor: 'pointer' }}>adnanfajlur</Typography>
            <div className={classes.divRight}>
              <Button className={classes.title} style={{ marginRight: 30 }} onClick={() => navigateTo('/contact-me')}>Contact Me</Button>
              <div style={{ display: 'flex' }}>
                <GithubIcon className={classes.iconHover} onClick={() => window.open('https://github.com/adnanfajlur')} />
                <InstagramIcon className={classes.iconHover} onClick={() => navigateTo('/mygram')} />
                <FacebookIcon className={classes.iconHover} onClick={() => window.open('https://www.facebook.com/fajar.rohman.73157')} />
              </div>
            </div>
          </div>
          <div className={classes.body}>
            {this.props.children}
          </div>
        </div>
      </Fragment>
    );
  }
}

Frame.propTypes = {
  classes: PropTypes.object.isRequired,
  children: PropTypes.node.isRequired,
};

export default withRoot(withStyles(styles)(Frame));



