import React, { Fragment } from 'react';
import Helmet from 'react-helmet';
import PropTypes from 'prop-types';
import { navigateTo } from 'gatsby-link';
import Frame from '../components/Global/Frame';

import Typography from 'material-ui/Typography';
import { withStyles } from 'material-ui/styles';
import Button from 'material-ui/Button';

const styles = theme => ({
  divContent: {
    marginTop: 50,
    marginBottom: 30,
    '@media (max-width: 670px)': {
      marginTop: 20,
    },
  },
  white: {
    color: '#FFF',
  },
  card: {
    width: 145,
    position: 'relative',
    height: 200,
    marginRight: 10,
    borderRadius: 5,
    cursor: 'pointer',
  },
  iconCard: {
    width: 35,
    height: 35,
    padding: 15,
    borderRadius: 100,
    margin: '20px auto 15px',
    background: 'rgba(0,0,0, 0.25)',
  },
  iconHover: {
    color: '#FFF',
    '&:hover': {
      fill: 'rgba(255, 255, 255, 0.5)',
    },
  },
  typoContent: {
    bottom: 0,
    margin: '20px 8px',
    color: '#FFF',
    position: 'absolute',
  },
  board: {
    minWidth: 300,
    background: '#FFF',
    marginLeft: 'auto',
    marginTop: 50,
    borderRadius: 5,
    padding: '17px 15px',
  },
  ul: {
    listStyle: 'circle',
    paddingLeft: '20px',
  },
  block: {
    display: 'flex',
  },
  '@media screen and (max-width: 710px)': {
    block: {
      display: 'block',
    },
    board: {
      margin: '30px 0px 20px',
      minWidth: 100,
    },
  },
});

const kotak = [
  {
    bg: '#52C9F5',
    content: 'Sample web with original data from instagram',
    title: 'Mygram',
    icon: 'icons/MyGram.svg',
  },
];

class Index extends React.Component {

  render() {
    const { classes } = this.props;
    return (
      <Fragment>
        <Helmet defaultTitle="adnanfajlur">
          <title>adnanfajlur</title>
          <html lang="en" />
        </Helmet>
        <Frame>
          <div className={classes.block}>
            <div>
              <div className={classes.divContent}>
                <Typography variant="display1" className={classes.white}>Hi there, I'm</Typography>
                <Typography variant="headline" className={classes.white}>Adnan Fajlur Rohman</Typography>
                <div style={{ marginTop: 40 }}>
                  <Typography className={classes.white}>A man born in 1999</Typography>
                  <Typography className={classes.white}>in a small village in the city of Rembang, Indonesia</Typography>
                  <Typography className={classes.white}>my passion is in programmers, especially front-end</Typography>
                </div>
              </div>
              <div style={{ display: 'flex' }}>
                {kotak.map((n, i) => (
                  <div
                    key={i}
                    className={classes.card}
                    style={{ backgroundColor: n.bg }}
                    onClick={() => navigateTo('/mygram')}
                  >
                    <div className={classes.iconCard}>
                      <img src={n.icon} alt="" />
                    </div>
                    <Typography className={classes.white} align="center">{n.title}</Typography>
                    <Typography variant="caption" className={classes.typoContent} align="center" >{n.content}</Typography>
                  </div>
                ))}
              </div>
            </div>
            <div className={classes.board}>
              <Typography variant="title">Things I like:</Typography>
              <ul className={classes.ul}>
                <li><Typography variant="subheading">Java Coffe</Typography></li>
                <li><Typography variant="subheading">Solve the problem with my hands</Typography></li>
                <li><Typography variant="subheading">Learning something new</Typography></li>
              </ul>
              <Typography variant="title">Technology that I like:</Typography>
              <ul className={classes.ul}>
                <li><Typography variant="subheading">VSCode</Typography></li>
                <li><Typography variant="subheading">Git</Typography></li>
                <li><Typography variant="subheading">ES6</Typography></li>
                <li><Typography variant="subheading">React</Typography></li>
                <li><Typography variant="subheading">Redux-Saga</Typography></li>
                <li><Typography variant="subheading">Gatsbyjs</Typography></li>
                <li><Typography variant="subheading">Nextjs</Typography></li>
                <li><Typography variant="subheading">Firebase</Typography></li>
                <li><Typography variant="subheading">Material UI</Typography></li>
                <li><Typography variant="subheading">Styled Component</Typography></li>
              </ul>
            </div>
          </div>
        </Frame>
      </Fragment>
    );
  }
}

Index.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Index);



